import React, {Component} from 'react'
import "../CSS/Mainpage.scss"
class Mainpage extends Component{
    render(){
        return(
            <div class="page">
                <main class="page_main">
                    <div class="page-header"></div>
                    <div id="content" class="page-content"></div>
                </main>
            </div>
        );
    }
}

export default Mainpage;